import { Component, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { IonicPage, NavController, NavParams, ViewController, ModalController } from 'ionic-angular';
import * as moment from 'moment';

import { CrmDataProvider } from '../../providers';

@IonicPage()
@Component({
  selector: 'page-crm-op-form',
  templateUrl: 'crm-op-form.html',
})
export class CrmOpFormPage {

  opForm: any;
  opKey: string;

  updateOpForm: any;
  submitType: string;
  
  // newClient: boolean;
  clientKey: string;
  updateClientForm: any;
  
  months: any;
  pspData: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private fb: FormBuilder,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController,
    private crmData: CrmDataProvider
  ) {
    this.pspData = this.navParams.data;
    this.buildForm(this.pspData);
    this.buildMonths();
    console.log(this.navParams.data);
  }

  ionViewDidLoad() {
  }

  // construye el formulario
  buildForm(form?) {
    this.opForm = this.fb.group({
      obra: ['', Validators.required ],
      client: ['', Validators.required],
      salesRep: [form.salesRep || '', Validators.required],
      closeMonth: ['', Validators.required],
      total: [form.total || '', Validators.required],
    });
  }
  // this.form.controls['name'].disabled();
  // para llamar a los modals
  lookClient() {
    let profileModal = this.modalCtrl.create('ClientSelectPage', {pablo:'pelotudo'});
    profileModal.onDidDismiss(data => {
      this.opForm.patchValue({
        client: data.payload.val().name
      });
      this.updateClientForm = data.payload.val();
      this.clientKey = data.key
      this.opForm.get('client').disable();
      console.log(this.opForm.get('client'), this.updateClientForm, this.clientKey);
    })
    profileModal.present();
  }

  lookOp() {
    let profileModal = this.modalCtrl.create('OpSelectPage');
    profileModal.onDidDismiss(data => {
      this.opForm.patchValue({
        obra: data.op.obra,
        closeMonth: data.op.closeMonth,
        client: data.op.client
      });
      this.updateOpForm = data.op
      this.opKey = data.key
      console.log(this.opForm.value, this.updateOpForm, this.opKey);
    })
    profileModal.present();
  }

  // construye los meses del select
  buildMonths() {
    this.months = [];
    for (let i = 0; i < 24; i++) {
      let today =  moment();
      let item = today.add(i, 'month').format('MM-YYYY');
      this.months.push(item);
    }
  }

  onSubmit() {
    if (this.submitType === 'new') {
      this.saveNewOPPsp();  
    } else {
    }
  }

  saveNewOPPsp2() {
    let form = this.opForm.value;
    form['psps'] = [this.pspData.num];
    let newOp = this.crmData.newOp(form);
    let checkedPsp = this.crmData.checkPsp(this.pspData.num);
    Promise.all([newOp, checkedPsp]).then( (ret) => {
      this.viewCtrl.dismiss();
    });
  }

  saveNewOPPsp() {
    let opForm
    let clientForm;
    let psp = {};
    psp[this.pspData.num] = true;

    if(!this.opKey) {
      opForm = this.opForm.value;
      opForm['psps'] = {}
    } 
    opForm['psps'][this.pspData.num] = true;

    if (!this.clientKey) {
      clientForm = {
        name: this.opForm.value.client,
        razSoc: {},
        ops: {},
        contacts: {}
      }
      clientForm['razSoc'][this.pspData.razSoc] = true;
    } else {
      if (!this.updateClientForm['razSoc'][this.pspData.razSoc]) {
        clientForm['razSoc'][this.pspData.razSoc] = true;
      }
    }
    
    this.crmData.saveNewOp(opForm, clientForm, psp, this.opKey, this.clientKey)
    .then( () => this.viewCtrl.dismiss());
  }

  addClient() {
    this.clientKey = undefined;
    this.opForm.controls.client.enabled();
    this.opForm.controls.client.reset();
  }

  addOp() {
    this.submitType = 'new';
    this.opKey = undefined;
  }

}



